@charset "utf-8";
body, h1, h2, h3, h4, h5, h6, hr, p, blockquote, dl, dt, dd, ul, ol, li, pre, form, fieldset, legend, button, input, textarea, th, td {
  margin: 0;
  padding: 0;
}

body, button, input, select, textarea, span, label, a {
  font: 14px/1.5 'Microsoft Yahei', 'Helvetica Neue', Arial, 'Hiragino Sans GB', STHeiti, 'STHeiti Light [STXihei]', sans-serif
}
body {
  background-repeat: no-repeat;
  background-size: cover;
}
button, input, select, textarea, h1, h2, h3, h4, h5, h6 {
  font-family: 'Microsoft Yahei', 'Helvetica Neue', Arial, 'Hiragino Sans GB', STHeiti, 'STHeiti Light [STXihei]';
  font-size: 100%;
}

button, input, select, textarea, label {
  vertical-align: middle;
  outline: none;
}

ul, ol {
  list-style: none;
}

a {
  text-decoration: none !important;
  vertical-align: baseline;
  cursor: pointer;
}

.y-row {
  position: relative;
  /*min-width: 1000px;*/
  /*max-width: 1200px;*/
  margin-left: auto;
  margin-right: auto;
  zoom: 1;
}

#header {
  width: 100%;
  height: 78px;
  border-bottom: 1px solid #e3e3e3;
  /*background-color: #fff;*/
  padding: 0px;
  padding-top: 10px;
}

#header .header-layout .logo {

}

#header .header-layout .header-nav {
  float: right;
}

#header .header-layout .header-nav span {
  display: inline-block;
  margin-right: 10px;
}

#header .header-layout .header-nav span img {
  height: 60px;
}

#header .header-layout .logo > img {
  height: 55px;
}

.content {
  position: relative;
  min-height: 520px;
  vertical-align: middle;
  /*background-color: #eaeaea;*/
  /*overflow: hidden;*/
}

.content #login-banner {
  width: 1200px;
  height: 520px;
  position: absolute;
  z-index: 100;
  overflow: hidden;
}

.content #login-banner a {
  display: inline-block;
  width: 100%;
}

.content #login-banner a img {
  width: 100%;
  height: 520px;
}

.content .login-module {
  width: 100%;
  height: 100%;
  position: absolute;
  left: -5%;
  z-index: 999;
}

.content .login-module .iframe-show {
  width: 285px;
  height: 385px;
  /*background-color: #ffffff;*/
  border-radius: 10px;
  box-shadow: 0 0 20px #999;
  padding-left: 28px;
  padding-right: 28px;
  float: right;
  margin-top: 5%;
}

.content .login-module .iframe-show .iframe-tit {
  text-align: center;
}

.content .login-module .iframe-show .iframe-tit h1 {
  font-size: 28px;
  color: #666666;
  padding-top: 58px;
}

.content .login-module .iframe-show .iframe-inp {
  display: block;
  margin-top: 16px;
}

#ie6-warning {
  display: none;
}

.content .login-module .iframe-show .iframe-inp label {
  display: inline-block;
  width: 16px;
  height: 16px;
  margin-bottom: 10px;
  margin-right: 4px;
  margin-left: 4px;
}

.content .login-module .iframe-show .iframe-inp span {
  display: inline-block;
}

.content .login-module .iframe-show .iframe-btn {
  margin-top: 44px;
}

.content .login-module .iframe-show .iframe-click {
  display: block;
  margin-top: 10px;
}

.content .login-module .iframe-show .iframe-click .click-l {
  float: left;
  color: #019fe9;
}

.content .login-module .iframe-show .iframe-click .click-r {
  float: right;
  color: #019fe9;
}

.btn-txt {
  background-color: #00a0e9;
  width: 100%;
  height: 36px;
  border: 0px;
  color: #fff;
  font-size: 16px;
  border-radius: 5px;
}

.fm-text {
  width: 200px;
  height: 32px;
  border: 1px solid #d5d5d5;
  padding-left: 10px;
}

.copyright-100 {
  clear: both;
  background: #fff;
  border-top: 1px solid #e5e5e5;
}

.copyright-100 .copyright {
  text-align: center;
  color: #999;
  font-size: 12px;
  padding: 35px 0 40px;
}

.copyright-100 .copyright .big {
  font-size: 14px;
  color: #3c3c3c;
  font-weight: 600;
}

.copyright-100 .copyright .fine {
  color: #333;
  margin-top: 10px;
}

.copy-em {
  font-style: normal;
  font-weight: bold;
}

.error-msg {
  margin-top: 5px;
  margin-left: 37px;
  color: red;
}

.bigBanner {
  position: absolute;
  z-index: 102;
  margin-top: 40%;
  margin-left: 35%;
}

.bigBanner span {
  display: inline-block;
  width: 10px;
  height: 10px;
  background-color: #f7f7f7;
  border-radius: 50px;
  margin-right: 30px;
  box-shadow: 0 0 4px #999;
}

.slick-dots {
  bottom: 30px !important;
}

@media screen and (max-width: 1200px) {
  .content .login-module .iframe-show {
    float: right;
    margin-top: 5%;
    margin-right: 5%;
  }
}
@media screen and (orientation: landscape) and   (max-width: 1200px) {
.content .login-module{
    left: -8%;
  }
}

@media screen and (orientation: landscape) and   (max-width: 1000px) {
  .content {
    width: 100%;
    /*background-color: #3ab4fd;*/
  }

  .ck-slide-dir {
    display: none;

  }

  .ck-slidebox {
    display: none;
  }
  .content .login-module{
    left: 0;
  }

  .ck-slide {
    width: 100% !important;
  }

  .content .login-module .iframe-show {
    margin: 10% auto;
    float: none;
  }

}

@media screen and (max-width: 600px) {
  .content {
    width: 100%;
    /*background-color: #3ab4fd;*/
  }

  .ck-slide-dir {
    display: none;

  }

  .ck-slidebox {
    display: none;
  }

  .header-nav {
    display: none;
  }

  .ck-slide {
    width: 100% !important;
  }

  .content .login-module{
    left: 0;
  }
  .content .login-module .iframe-show {
    margin: 10% auto;
    float: none;
  }
}

.ck-slide {
  width: 1200px;
  height: 520px;
  margin: 0 auto;
}

.ck-slide ul.ck-slide-wrapper {
  height: 320px;
}

#ie6-warning {
  background: rgb(255, 255, 225);
  top: 0;
  left: 0;
  font-size: 12px;
  color: #333;
  width: 100%;
  padding: 2px 15px 2px 23px;
  text-align: left;
  display: none;
}

.solutionImg {
  margin: 0;
  padding: 0;
  width:"408px";
  height:"288px";
}








